@import url(https://fonts.googleapis.com/css?family=Roboto:500);

:root {
  --backgroundColor: #6998AB;
  --textColor: #1A374D;
  --buttonColor:  #B1D0E0;
  --titleColor: #1A374D;
  --cardColor: #ECF4F3;
}

body {
  background-color: var(--backgroundColor);
}
.App {
  text-align: center;
  padding: 2% 5%;
}

.App * {
  box-sizing: border-box;
}

ul {
  padding: 0;
  list-style: none;
}

p {
  margin: 5px 0;
}

.display-none {
  display: none;
}

.data-container {
  /* height: 50vh; */
  /* width: 100%; */
}
.showSuggestions {
  display: block;
}

input[type="text"],
input[type="number"] {
  padding: 8px 6px 8px 10px;
  border-radius: 4px;
  outline: none;
  border: 1px solid #ccc;
  font-size: 14px;
  line-height: 20px;
  align-self: center;
  height: 38px;
}

.numberInput {
  width:4rem;
  margin: 1rem;
}
.city {
  width: 250px;
}

.CitySearch {
  display: flex;
  flex-direction: column;
  /* flex-basis: 300px; */
  /* max-width: 300px; */
  margin: auto;
  position: relative;
}

.alert {
  height: 2rem;

}
.suggestions {
  width: 250px;
  position: absolute;
  z-index: 2;
  align-self: center;
  margin: 0;
  top: 4rem;
  font-size: 14px;
  box-shadow: 10px 10px 16px -12px rgba(0, 0, 0, 0.75);
}

.suggestions > li {
  text-align: left;
  padding: 8px 6px 8px 10px;
  background-color: #eee;
  cursor: default;
}

.suggestions > li:last-child {
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}

.suggestions > li:hover {
  background-color: #bcdcdb;
}

.suggestion-btn {
  border: none;
  background-color: #eee;
  width: 100%;
  height: 100%;
  margin: 0;
}

.EventList {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  height: auto;
}
.Event {
  padding: 2rem 2rem 3rem;
  margin: 10px;
  width: 90vw;
  text-align: left;
  border-radius: 12px;
  background-color: var(--cardColor);
  color: var(--textColor);
  height: 90%;
}

.Event h1 {
  color: var(--titleColor);
  display: flex;
  flex-direction: column;
}

.eventDetails {
}

.details-btn {
  background-color: var(--buttonColor);
  color: black;
  margin-left: 80%;
  padding: 12px;
  border-radius: 6px;
  border: none;
  outline: none;
  cursor: pointer;
  align-self: flex-end;
}

.details-btn:hover {
  opacity: 0.9;
}

.name {
  font-size: 18px;
  font-weight: 600;
}

.group-name {
  text-transform: uppercase;
  font-size: 14px;
}

.going,
.visibility {
  font-style: italic;
}

.description {
  overflow-x: hidden;
}

.NumberOfEvents {
  width: 200px;
  margin: 20px auto;
  text-align: center;
}

.button-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 20px;
  padding: 20px;
  font-family: sans-serif;
}

.data-vis-wrapper {
  display: flex;
  align-items: center;
  flex-direction: column;
}
.data-vis-wrapper .recharts-responsive-container {
  min-width: 100%;
  display: flex;
}
.recharts-surface {
  overflow: visible;
}

.login-button {
  color: #494949;
  text-transform: uppercase;
  text-decoration: none;
  background: #ffffff;
  padding: 20px;
  border: 4px solid #494949;
  display: inline-block;
  transition: all 0.4s ease 0s;
  min-width: 10rem;
  margin: 1rem;
}

.google-btn {
  width: 184px;
  height: 42px;
  margin: 1rem;
  background-color: #4285f4;
  border-radius: 2px;
  box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.25);
}
.google-btn:hover {
  box-shadow: 0 0 6px #4285f4;
}
.google-btn:active {
  background: #1669f2;
}
.google-icon-wrapper {
  position: absolute;
  margin-top: 1px;
  margin-left: 1px;
  width: 40px;
  height: 40px;
  border-radius: 2px;
  background-color: #fff;
}
.google-icon {
  position: absolute;
  margin-top: 11px;
  margin-left: -8px;
  width: 18px;
  height: 18px;
}
.btn-text {
  float: right;
  margin: 11px 11px 0 0;
  color: #fff;
  font-size: 14px;
  letter-spacing: 0.2px;
  font-family: "Roboto";
}

.title {
  color: var(--titleColor);
  font-size: 4rem;
  margin: 0;
  padding: 0;
}
@media only screen and (min-width: 768px) {
  .Event {
    width: 40vw;
  }
}

@media only screen and (min-width: 992px) {
  .data-vis-wrapper {
    flex-direction: row;
  }
  .data-vis-wrapper .recharts-responsive-container {
    flex-basis: 50%;
    max-width: 50%;
    min-width: auto;
    margin: 0 auto;
  }
}
